<template>
  <div class="wrap">
    <nav>
      <div class="group">
        <div class="g1">一组</div>
        <div class="g2">二组</div>
        <div class="g3">三组</div>
      </div>
      <h1>2019防守排行（Seatin）</h1>
      <h4>Boss位</h4>
      <div class="rank">
        <div class="herocard" v-for="hero in lv1" :key="hero">
          <Aq :championName="hero" />
        </div>
      </div>
      <h4>混蛋级</h4>
      <div class="rank">
        <div class="herocard" v-for="hero in lv2" :key="hero">
          <Aq :championName="hero" />
        </div>
      </div>
      <h4>恶棍级</h4>
      <div class="rank">
        <div class="herocard" v-for="hero in lv3" :key="hero">
          <Aq :championName="hero" />
        </div>
      </div>
      <h4>蛋疼级</h4>
      <div class="rank">
        <div class="herocard" v-for="hero in lv4" :key="hero">
          <Aq :championName="hero" />
        </div>
      </div>
      <h4>头疼级</h4>
      <div class="rank">
        <div class="herocard" v-for="hero in lv5" :key="hero">
          <Aq :championName="hero" />
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import Aq from './Aq'
export default {
  data () {
    return {
      rank: ['lv1', 'lv2', 'lv3', 'lv4', 'lv5'],
      lv1: [
        'Domino',
        'MisterSinister',
        'Havok',
        'Korg',
        'Medusa',
        'TheChampion',
        'CorvusGlaive',
        'Dormammu',
        'IronMan(IW)',
        'MODOK',
        'SpiderMan(Classic)',
        'Thing'
      ],
      lv2: [
        'Domino',
        'MisterSinister',
        'Havok',
        'Killmonger',
        'Korg',
        'Medusa',
        'Dormammu',
        'Mephisto',
        'IronMan(IW)',
        'MODOK',
        'SpiderMan(Classic)',
        'Thing'
      ],
      lv3: [
        'Nightcrawler',
        'Iceman',
        'Sabretooth',
        'EmmaFrost',
        'Hyperion',
        'TheChampion',
        'Morningstar',
        'Magik',
        'Sentinel',
        'Darkhawk',
        'SpiderMan(MilesMorales)',
        'Void',
        'Wasp',
        'Hulk(Ragnarok)',
        'SpiderGwen'
      ],
      lv4: [
        'Cable',
        'Bishop',
        'OmegaRed',
        'AgentVenom',
        'Blade',
        'Kingpin',
        'Heimdall',
        'CorvusGlaive',
        'TheHood',
        'UnstoppableColossus',
        'Juggernaut',
        'Mordo',
        'Ultron',
        'Ultron(Classic)',
        'Punisher(2099)',
        'SpiderMan(Stark)',
        'RedSkull',
        'GreenGoblin',
        'Quake',
        'Electro'
      ],
      lv5: [
        'Rogue',
        'BlackPanther(CivilWar)',
        'Thor(Ragnarok)',
        'Punisher',
        'Masacre',
        'Taskmaster',
        'MoonKnight',
        'BlackWidow',
        'NightThrasher',
        'Ægon',
        'SpiderMan(Symbiote)',
        'KingGroot',
        'Venom',
        'SymbioteSupreme',
        'Yondu',
        'Vision',
        'WarMachine',
        'Rhino',
        'Abomination',
        'Yellowjacket',
        'Hulk'
      ]
    }
  },
  components: { Aq }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/juice
.wrap
  width: 100%
  height: 100vh
  over-flow: hidden
  box-sizing: border-box
  .group
    margin: 2rem auto

    background: #aaa
    width: 300px
    display: flex
    border-radius: 5px
    overflow: hidden
    border: 1px solid #888
    .g1, .g2, .g3
      +size(100px, 40px)
      box-sizing: border-box
      text-align: center
      line-height: 40px
      display: inline
      cursor: pointer
      &:hover
        background: orange
    .g1, .g2
      border-right: 1px solid #888
</style>



